@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@500&family=Poppins:ital,wght@1,800&display=swap');
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	background-color: #181818;
  font-family: 'Barlow', sans-serif !important;
}

.heading_style {
	padding: 30px 0;
	text-align: center;
	font-family: sans-serif;
	text-transform: uppercase;
	letter-spacing: 5px;
	font-weight: 600;
	color: #E50914;
	background-color: black;
	font-size: 2rem;
	margin-bottom: 30px;
}

.cards {
	width: 100%;
	height: auto;
}

.card {
	margin: 6%;
	transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	background-color: #fff;
	width: 21.25%;
	border-radius: 13px;
	/* box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1); */
  box-shadow: 20px 20px #3e3a3a;
	float: left;
}

.card:hover {
	box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 1);
	transform: scale(1.06, 1.06);
}

.card_img {
	width: 100%;
	height: 235px;
	border-top-left-radius: 12px;
	border-top-right-radius: 12px;
}

.card_info {
	background-color: #fff;
	border-bottom-left-radius: 12px;
	border-bottom-right-radius: 12px;
	padding: 16px 24px 24px 24px;
}

.card_category {
	font-family: sans-serif;
	text-transform: uppercase;
	font-size: 13px;
	letter-spacing: 2px;
	font-weight: 500;
	color: #868686;
	background-color: #fff;
}

.card_title {
	margin-top: 5px;
	margin-bottom: 10px;
	font-family: sans-serif;
	background-color: #fff;
	text-transform: capitalize;
}

button {
	padding: 5px 8px;
	font-family: sans-serif;
	text-transform: uppercase;
	font-size: 13px;
	letter-spacing: 2px;
	font-weight: 500;
	color: #868686;
  padding: 10px 15px;
  border-radius: 8px;
	background-color: #fff;
	outline: none;
	border: 1px solid red;
	cursor: pointer;
}

button:hover{
  background-color: red;
  color: black;
}

@media (max-width: 768px) {
	.heading_style{
		font-size: 1.5rem;
		padding: 20px;
	}
	.card {
		float: none;
		margin: 5%;
		width: 90%;
		margin-bottom: 80px;
	}
}
